@import url("https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Gentium+Book+Basic:ital@1&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap");
.form-design-signup {
  margin: auto;
  margin-top: 3.5%;
  margin-bottom: 5%;
  width: 500px;
  height: 570px;
  opacity: 0.9;
  border-radius: 2em 0em;
}

#signupBg {
  background-image: url(./micah-williams-lmFJOx7hPc4-unsplash.jpg);
  background-size: cover;
  background-position: center;
}
.signUpLabel {
  font-family: "Gentium Book Basic", serif;
  font-weight: 500;
}

#otpBg {
  background-image: url(./micah-williams-lmFJOx7hPc4-unsplash.jpg);
  background-position: center;
  background-size: cover;
}
.otpLayout {
  margin: auto;
  margin-top: 13%;
  margin-bottom: 12.6%;
  width: 500px;
  height: 320px;
  opacity: 0.9;
  border-radius: 2em 0em;
}

@media only screen and (max-width:425px)
{
    .form-design-signup {
        margin: auto;
        margin-top: 10%;
        margin-bottom: 13%;
        width: 350px;
        height: 570px;
        opacity: 0.9;
        border-radius: 2em 0em;
      }
}